import React, { Component } from 'react';

import Header from '../components/Header';
import Main from '../components/Main';

class App extends Component {
  render() {
    return (
      <div className="screen">
        <Header />
        <Main props={this.props} />
        <footer className="screen-footer" />
      </div>
    );
  }
}

export default App;



