import React from 'react';

import {
  BADGES_COLORS,
  COLORS
} from '../../constants';

const Results = ({props}) => {
  const {scorer} = props;
  console.log(scorer);
	return (
    <main className="screen-main screen-scorer-results">
      <section className="content mode-scorer">
        <h1>Results</h1>
        <ul className="content-box ranking">
        {
          scorer.rank.map((playerIndex, i) => {
            // Add special class to winner
            let rankClasses = 'position';
            if (i === 0) rankClasses += ' winner';

            return (
              <li key={ playerIndex } className={ rankClasses }>
                <div className="pawn">
                  <img src={`/images/scorer/player-${COLORS[playerIndex]}.svg`} alt={`1-players`} />
                  { i === 0 ? <span className="crown" /> : null }
                </div>
                <div className="info">
                  <p className="points">{`${scorer.total[playerIndex]} points`}</p>
                  <div className="badges">
                  {
                    scorer.badges[playerIndex].map((badge) => {
                      console.log(badge);
                      let badgeClasses = `badge ${BADGES_COLORS[badge]}`;
                      return (
                        <span key={badge} span className={ badgeClasses } />
                      );
                    })
                  }
                  </div>
                </div>
              </li>
            );
          })
        }

        </ul>
      </section>
    </main>
  );
};

export default Results;



